.slider {
  height: calc(100vh - 170px);
  width: 100vw;
  position: relative;
  overflow: hidden;

  .container {
    width: 300vw;
    height: 100%;
    display: flex;
    transition:  0.5s ease;

    img {
      width: 100vw;
      height: 100%;
      object-fit: cover;
    }
  }

  .icons {
    width: fit-content;
    display: flex;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 20px;
    gap: 10px;
    margin: auto;

    .icon {
      width: 50px;
      height: 50px;
      border: 1px solid #999;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      color: white;
    }
  }

  @media screen and (max-width: 767px) {
    .container {
      width: 600vw;
    }

    .icons {
      bottom: 10px;
    }

    .icon {
      width: 40px;
      height: 40px;
    }
  }
}
